
.imgBitCoin {
    width: 200px;
    margin-top: 20px;
    animation: rotation 10s infinite linear;
  }
  

  
  @keyframes rotation {
    from {
      transform: rotatey(0deg);
    }
    to {
      transform: rotatey(359deg);
    }
    
  }

